import React, { Component } from 'react';
import ImageGallery from 'react-image-gallery';
import "react-image-gallery/styles/css/image-gallery.css";
import './App.css';

class App extends Component {
  render() {

    const images = Array.from(Array(9).keys())
        .map(function(x) {
              return {
                original: "/assets/mihalykoles/g_koles_mihaly_0000"+(x+1)+".jpeg",
                thumbnail: "/assets/mihalykoles/g_koles_mihaly_0000"+(x+1)+".jpeg"
              };
        });
    console.log(images);

    return (
      <ImageGallery items={images} slideInterval={10000} />
    );
  }
}

export default App;
